body {
    overflow-x: hidden;
    width: 100%;
}   

h3{
    background: #3984E8;
    color: #ffffff;
    border-radius: 0 0 20px 20px;
    padding: 25px;
    font-size: 1.3em;
}

.opcao{
    display: block;
    margin: 5px;
    border-bottom: solid 1px #CCC;
    margin-bottom: 10px;
    color: slategrey;
}
#auth{
    color: darkslategray;
}
.google_code{
    color: black;
    background: grey;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 25px;
    width: 70px;
    height: 70px;
    margin: 20px auto;
}

.confirmar{
    color: white !important;
}

.mdc-radio{
    display: flex;
    align-items: center;
    margin: 10px 20px;
    padding: 10px;
    font-size: 1.2em;
}

.mdc-radio label{
    padding: 0 10px;
}

.mdc-touch-target-wrapper{
    display: flex;
    justify-content: center;
}

main, header{
    max-width: 1200px;
    margin: 0 auto;
}

.codigo{
    display: none;
    width: 300px;
    display: block;
    margin: 20px auto;
}

.finalizacao, 
.texto_erro{
    width: 80%;
    display: block;
    margin: 20px auto;
    text-align: center;
}

.texto_erro{
    color: red;
}

.texto_inicial, 
.texto_confirmacao , 
.texto_codigo,
.texto_erro,
.codigo{
    display: none ;
}

.main_principal{
    display: block;
}

.main_finalizado{
    width: 100vw;
    height: 100vh;
    display: none;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: #3984E8;
}

.main_finalizado h2,
.main_finalizado h4,
.main_finalizado h5{
    margin: 10px;
}