:root{
    --cinza: #333333;
}

@font-face {
    font-family: 'SF Pro Display';
    src: local('SF Pro Display'), url(./../Fonts/SFPRODISPLAYREGULAR.OTF) format('truetype');
}

@font-face {
    font-family: 'SF Pro Display Bold';
    src: local('SF Pro Display Bold'), url(./../Fonts/SFPRODISPLAYBOLD.OTF) format('truetype');
}

body{
    font-family: 'SF Pro Display';
}

.menu_secundario, .menu_superior{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 10px;
}

.menu_superior{
    background-color: var(--cinza);
}

.menu_superior:first-child{
    margin-top: -8px;
}

.menu_secundario{
    border-bottom: solid 1px #ccc;
}

.menu_secundario span{
    font-family: 'SF Pro Display Bold';
    font-size: 1.1rem;
    color: #494949 !important;
}

.menu_secundario img{
    margin-right: -5px !important;
}

.appleIntro{
    display: flex;
    justify-content: center;
}

.content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.content span{
    margin: 20px 0;
    font-size: 17px;
    color: #494949 !important;
}

h1{
    font-size: 2.3rem;
}

.input_email{
    width: 270px;
    height: 44px;
    border-radius: 6px;
    border: 2px solid #d6d6d6;
    font-size: 1.2em;
    padding: 0px 16px;
}

.btn_enter{
    border: none;
    background: none;
    position: relative;
    margin-top: -40px;
    margin-left: 250px;
}

.input_senha{
    display: none;
    width: 270px;
    height: 44px;
    border-radius: 0 0 6px 6px;
    border: 2px solid #d6d6d6;
    font-size: 1.2em;
    padding: 0px 16px;
    margin-top: -2px;
}

.com_senha{
    border-radius: 6px 6px 0 0;
}


.input_email:focus, .input_senha:focus{
    outline: none !important;
    border:2px solid #0070C9;
}

.lembrar{
    margin: 50px;
    color: #494949;
}

.pop-container{
    display: none;
}

.pop-container .pop-bottom {
    background-color: #fae9a3;
    position: relative;
    width: 284px;
    border-radius: 5px;
    border: 1px solid rgba(185,149,1,.47);
    box-shadow: 0 5px 10px 2px rgb(0 0 0 / 10%);
    margin-top: -107px;
    padding: 11px;
}

.pop-container .pop-bottom:before {
    width: 15px;
    height: 15px;
    background-color: #fae9a3;
    content: "";
    position: absolute;
    left: 47.2%;
    -webkit-transform: rotate(135deg) skewX(5deg) skewY(5deg);
    -ms-transform: rotate(135deg) skewX(5deg) skewY(5deg);
    -o-transform: rotate(135deg) skewX(5deg) skewY(5deg);
    transform: rotate(135deg) skewX(5deg) skewY(5deg);
    top: -8px;
    border-left: 1px solid rgba(185,149,1,.47);
    border-bottom: 1px solid rgba(185,149,1,.47);
    box-shadow: -1px 1px 2px -1px rgb(185 149 1 / 47%);
}
